import { useState } from "react";
import ReactDOM from "react-dom";

import "./styles.css";
// import send from "./script";
const ethers = require("ethers");

export default function App() {
  const [bnbfrom, setBnb] = useState("");
  const [walletfrom, setWallet] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    send();
    // console.log(bnbfrom);
    // console.log(walletfrom);
  };

  // ตั้งค่า เชน
  const providerRPC = {
    development: {
      name: "BSC",
      rpc: "https://data-seed-prebsc-2-s3.binance.org:8545/",
      chainId: 97
    }
  };
  const provider = new ethers.providers.StaticJsonRpcProvider(
    providerRPC.development.rpc,
    {
      chainId: providerRPC.development.chainId,
      name: providerRPC.development.name
    }
  ); //Change to correct network

  // Variables
  const account_from = {
    privateKey:
      "e60f889abd8737698974d2be95b0cc297d34e369a24be08609bff9a45358a97a"
  };
  const addressTo = walletfrom;

  // Create Wallet
  let wallet = new ethers.Wallet(account_from.privateKey, provider);

  /*
   -- Create and Deploy Transaction --
*/
  const send = async () => {
    console.log(
      // `Attempting to send transaction from ${wallet.address} to ${addressTo}`
      `send transaction from to ${addressTo}`
    );

    // Create Tx Object
    const tx = {
      to: addressTo,
      value: ethers.utils.parseEther(bnbfrom)
    };

    // Sign and Send Tx - Wait for Receipt
    const createReceipt = await wallet.sendTransaction(tx);
    await createReceipt.wait();
    console.log(`Transaction successful with hash: ${createReceipt.hash}`);
  };

  return (
    <div className="App">
      <form onSubmit={handleSubmit}>
        <label>
          <input
            type="number"
            placeholder="จำนวน BNB"
            value={bnbfrom}
            onChange={(e) => setBnb(e.target.value)}
          />
        </label>
        <label>
          <input
            type="text"
            placeholder="Wallet address"
            value={walletfrom}
            onChange={(e) => setWallet(e.target.value)}
          />
        </label>
        <button>Send</button>
      </form>
    </div>
  );
}
